              name:'销售额',
              type:'line',
              yAxisIndex: 1,
              color:'#00fcff',
              data:clone[2],
              areaStyle: {
                normal: {
                  color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [{
                    offset: 0,
                    color: 'rgba(55,248,255, 0.25)'
                  }, {
                    offset: 0.8,
                    color: 'rgba(0,116,219,0.25)'
                  }], false),
                  shadowColor: 'rgba(0, 0, 0, 0.5)',
                  shadowBlur: 10
                }
              }
            },
            {
              name:'销售量',
              type:'bar',
              data:clone[1],
              barWidth : 15,
              "itemStyle": {
                "normal": {
                  barBorderRadius: [30, 30, 0, 0],
                  "color": {
                    "type": "linear",
                    "x": 0,
                    "y": 0,
                    "x2": 0,
                    "y2": 1,
                    "colorStops": [
                      {
                        "offset": 0,
                        "color": "rgba(101,255,213,1)"
                      },
                      {
                        "offset": 0.5,
                        "color": "rgba(101,255,239,1)"
                      },
                      {
                        "offset": 1,
                        "color": "rgba(55,248,255,1)"
                      }
                    ],
                    "globalCoord": false
                  }
                }
              }
            },
          ]
        };
        myChart.setOption(options);
        myChart.resize();
      }

    }
  }
</script>



